    private summonBricks(){//是否定义为私有不影响，下同
        //实例化与应用到节点树
        const newBrick=instantiate(this.Brick);
        this.summonBrickPlace.addChild(newBrick);
        newBrick.setPosition(v3(400,200,0));
    }
    
